<template>
    <div>
        <div style="width: 200px;height: 500px;border: 1px solid #000;"  @contextmenu.stop="cont" >
        <!-- @mousemove="mouse"  @click="clic"-->
           <button>按钮</button>   
           <br>
           <span>在这个区域内点击右键</span>
      </div>
      <div :style="{color: 'red' ,fontSize: '16px', backgroundColor: '#f5f5f5',border: `1px solid #000`,position: 'fixed',top:yyy+'px',left:xxx+'px',height:'120px'}"  v-show="isPopupVisible" ref="popup">
              第一行
              <br>
              第二行
              <br>
              第三行
              <br>
              第四行
              <br>
              第五行
           </div>
           <div style="width: 50px;height: 100px;border: 1px solid #000;" @contextmenu.stop="fn"> 
                    组件的点击这个区域
           </div>
           <YouJ ref="youjref"></YouJ>
    </div>
</template>

<script>
import YouJ from './zujiana.vue'
export default {
  components:{
    YouJ
    },
    data () {
        return {
        isPopupVisible:false,
        xxx:0,
        yyy:0
        }
    },
    mounted() {
            document.addEventListener('contextmenu', this.handleRightClickOutside);
            document.addEventListener('click', this.handleRightClickOutside);
          },
    beforeDestroy() {
            document.removeEventListener('contextmenu', this.handleRightClickOutside);
          },
    methods:{
      // 鼠标右键
      cont(e){
        this.isPopupVisible=true
        let mouseX = e.clientX;
        let mouseY = e.clientY;
        e.preventDefault(); // 阻止默认的右键菜单弹出
        console.log(mouseX,mouseY);
        this.xxx=mouseX
        this.yyy=mouseY
        console.log(this.xxx,this.yyy);
      },
      handleRightClickOutside(event) {
        event.preventDefault();// 阻止默认的右键菜单弹出
    if (this.isPopupVisible) {
      const targetElement = event.target;
      const popupElement = this.$refs.popup; // 弹框元素的引用，请根据实际情况修改
      console.log(targetElement);
      console.log(popupElement);
      if (!popupElement.contains(targetElement)) {
        console.log('点击外面的时候让他变成false');
        this.isPopupVisible = false;
        }
      }
      },
      // 组件的方法
      fn(e){
                 this.$refs.youjref.cont(e)
      }
    }
}
</script>
 
<style lang = "scss" scoped>
    
</style>